import { Alert, Typography } from 'antd';
import { useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import { AuthForm } from '../../components';
import { AuthFormValues, getAuth } from '../../features/user/userApi';
import { clearError, selectUserError } from '../../features/user/userSlice';
import styles from './Login.module.css';
const { Title } = Typography;

export const Login = () => {
  const error = useAppSelector(selectUserError);
  const location = useLocation();
  const dispatch = useAppDispatch();

  const SignIn = (formValues: AuthFormValues) => {
    dispatch(getAuth(formValues));
  };

  useEffect(() => {
    return () => {
      dispatch(clearError());
    };
  }, [location, dispatch]);

  return (
    <div className={styles.loginFormContainer}>
      <div>
        <Title className={styles.loginFormTitle}></Title>
        <AuthForm buttonText='' onFinish={SignIn} />
        <div>
           <Link to='signup'></Link>
        </div>

        {error && (
          <Alert
            className={styles.loginError}
            message={error}
            type='error'
            showIcon
            closable
          />
        )}
      </div>
    </div>
  );
};
